export const Person = ({ name, age, isMarried, partnerName, sex }) => {
  // sex === 'm' ? 'is my wife' : ;

  const isWife = () => {
    if (sex === 'm') {
      return 'is my wife';
    }

    return 'is my husband';
  };

  return (
    <div className="App">
      <section className="Person">
        <h2 className="Person__name">{`My name is ${name}`}</h2>
        <p className="Person__age">{age && `I am ${age}`}</p>
        <p className="Person__partner">
          {isMarried ? `${partnerName} ${isWife(sex)}` : 'I am not married'}
        </p>
      </section>
    </div>
  );
};
